import React, { forwardRef } from 'react';
import { elementType, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-text';

const Typography = forwardRef(({
  children,
  className,
  tag: Tag,
  variant,
  ...remainingProps
}, ref) => (
  <Tag
    className={classnames({
      [`${CSS_PREFIX}--${variant}`]: Boolean(variant)
    }, className ) || undefined}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

Typography.propTypes = {
  /**
  * Content
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType,

  /**
  * Variant
  */
  variant: oneOf([
    'body',
    'body--alt',
    'caption',
    'display-s',
    'headline',
    'headline--alt',
    'subtitle',
    'subtitle--alt',
    'title',
    'title--alt'
  ])
};

Typography.defaultProps = {
  tag: 'p'
};

Typography.displayName = 'Typography';

export default Typography;
