import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';
import Col from '../col';

const CSS_PREFIX = 'vds-footer-region-col';

const FooterRegionCol = ({ children, className, ...remainingProps }) => (
  <Col
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </Col>
);

FooterRegionCol.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

FooterRegionCol.displayName = 'FooterRegionCol';

export default FooterRegionCol;
