import React from 'react';
import { bool, func, node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import Grid from '../grid';
import Row from '../row';
import Col from '../col';

const CSS_PREFIX = 'vds-progress';
const CSS_STATE = 'vds-state';
const SIZES = {
  large: {
    cx: 36,
    cy: 36,
    r: 32,
    strokeDasharray: 201.062,
    strokeDashoffset: 56.2973,
    width: 72
  },
  small: {
    cx: 24,
    cy: 24,
    r: 20,
    strokeDasharray: 125.664,
    strokeDashoffset: 35.1858,
    width: 48
  }
};

const Progress = ({
  annotation,
  ariaLabel,
  ariaLabelDeterminateText,
  className,
  determinate,
  linearBorderStyle,
  max,
  min,
  size,
  type,
  value,
  ...remainingProps
}) => {
  if (type === 'linear') {
    return (
      <div
        className={classnames(CSS_PREFIX, {
          [`${CSS_STATE}--determinate`]: Boolean(determinate),
          [`${CSS_PREFIX}--${linearBorderStyle}`]: Boolean(linearBorderStyle)
        }, className)}
        {...remainingProps}
      >
        <div className={classnames(`${CSS_PREFIX}-background`)} />
        <div
          aria-label={`${ariaLabel}${determinate ? ariaLabelDeterminateText(value) : ''}`}
          aria-valuemax={determinate ? `${max}` : undefined}
          aria-valuemin={determinate ? `${min}` : undefined}
          aria-valuenow={determinate ? value : undefined}
          className={classnames(`${CSS_PREFIX}-bar`)}
          role="progressbar"
          style={{
            width: determinate ? `${((value / max) * 100).toFixed(2)}%` : undefined
          }}
        />
        {
          determinate
            ? annotation({ containerClassName: `${CSS_PREFIX}-label`, max, value })
            : undefined
        }
      </div>
    );
  }

  return (
    <div
      className={classnames(`${CSS_PREFIX}--spinner`, {
        [`${CSS_STATE}--determinate`]: Boolean(determinate),
        [`${CSS_STATE}--small`]: size === 'small'
      }, className)}
      {...remainingProps}
    >
      <svg className={`${CSS_PREFIX}--spinner-track`} height={SIZES[size].width} width={SIZES[size].width}>
        <circle
          className={`${CSS_PREFIX}--spinner-background`}
          cx={SIZES[size].cx}
          cy={SIZES[size].cy}
          r={SIZES[size].r}
          style={{
            strokeDasharray: `${SIZES[size].strokeDasharray}, ${SIZES[size].strokeDasharray}`,
            strokeDashoffset: `${SIZES[size].strokeDashoffset}`
          }}
        />
        <circle
          className={`${CSS_PREFIX}--spinner-bar`}
          cx={SIZES[size].cx}
          cy={SIZES[size].cy}
          r={SIZES[size].r}
          style={{
            strokeDasharray: `${SIZES[size].strokeDasharray}, ${SIZES[size].strokeDasharray}`,
            strokeDashoffset:
              type === 'determinate'
                ? SIZES[size].strokeDasharray - SIZES[size].strokeDasharray / (100 / value)
                : SIZES[size].strokeDashoffset
          }}
        />
      </svg>
      <div className="vds-progress-label">{`${value}%`}</div>
    </div>
  );
};

Progress.propTypes = {
  /**
   * Annotation
   */
  annotation: func,

  /**
   * Label
   */
  ariaLabel: string,

  /**
   * Aria label loaded text
   */
  ariaLabelDeterminateText: func,

  /**
  * @ignore
  */
  children: oneOfType([
    func,
    node
  ]),

  /**
   * @ignore
   */
  className: string,

  /**
   * Determinate
   */
  determinate: bool,

  /**
   * Linear border style
   */
  linearBorderStyle: oneOf(['rounded']),

  /**
   * Value max
   */
  max: number,

  /**
   * Value min
   */
  min: number,

  /**
   * Size
   */
  size: oneOf(['large', 'small']),

  /**
   * Type
   */
  type: oneOf(['spinner', 'linear']),

  /**
   * Value
   */
  value: number
};

Progress.defaultProps = {
  annotation: ({ containerClassName, max, value }) => ( // eslint-disable-line
    <Grid className={classnames(containerClassName)} type="fluid">
      <Row>
        <Col>
          {`${value} / ${max}`}
        </Col>
        <Col textAlign="right">
          {`${value}%`}
        </Col>
      </Row>
    </Grid>),
  ariaLabel: 'Progress bar',
  ariaLabelDeterminateText: value => `, ${value} loaded`,
  max: 100,
  min: 0,
  size: 'large',
  type: 'linear',
  value: 0
};

Progress.displayName = 'Progress';

export default Progress;
