import React, { cloneElement, forwardRef } from 'react';
import { element, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-footer-logo';

const FooterLogo = forwardRef(({ className, tag, ...remainingProps }, ref) => (
  cloneElement(tag, {
    className: classnames(CSS_PREFIX, className),
    ref,
    ...remainingProps
  })
));

FooterLogo.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: element
};

FooterLogo.defaultProps = {
  tag: <img /> // eslint-disable-line
};

FooterLogo.displayName = 'FooterLogo';

export default FooterLogo;
