import React, { forwardRef } from 'react';
import { bool, elementType, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-footer';

// send to VDS Web
const stickyStyles = {
  flexShrink: '0'
};

const Footer = forwardRef((props, ref) => {
  const {
    children,
    className,
    position,
    role,
    tag: Tag,
    sticky,
    ...remainingProps
  } = props;

  return (
    <Tag
      className={classnames({
        [`${CSS_PREFIX}`]: Boolean(position) === false,
        [`${CSS_PREFIX}--${position}`]: Boolean(position)
      }, className)}
      ref={ref}
      role={role}
      style={
        sticky
          ? remainingProps.styles
            ? { ...stickyStyles, ...remainingProps.styles }
            : stickyStyles
          : remainingProps.styles
            ? remainingProps.styles
            : undefined
      }
      {...remainingProps}
    >
      {children}
    </Tag>
  );
});

Footer.propTypes = {
  /**
   * Additional content
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Copyright text
   */
  copyright: string,

  /**
   * Position
   */
  position: oneOf(['fixed', 'relative']),

  /**
   * A11y Role
   */
  role: string,

  /**
  * Sticky footer
  */
  sticky: bool,

  /**
  * Tag
  */
  tag: elementType
};

Footer.defaultProps = {
  role: 'contentinfo',
  tag: 'footer'
};

Footer.displayName = 'Footer';

export default Footer;
