import React from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-content';

// send to VDS Web
const containsStickyFooterStyles = {
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100%'
};

const Content = ({ children, className, containsStickyFooter, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
    style={
      containsStickyFooter
        ? remainingProps.styles
          ? { ...containsStickyFooterStyles, ...remainingProps.styles }
          : containsStickyFooterStyles
        : remainingProps.styles
          ? remainingProps.styles
          : undefined
    }
  >
    {children}
  </div>
);

Content.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Contains sticky footer
   */
  containsStickyFooter: bool
};

Content.displayName = 'Content';

export default Content;
