import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import React, { Suspense } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom';
import { Provider, subscribe } from 'react-contextual';

import './index.css';
// import registerServiceWorker from './registerServiceWorker';

import Alert from '../components/alert';
import AlertIcon from '../components/alert-icon';
import AlertLabel from '../components/alert-label';
import AlertLabelTitle from '../components/alert-label-title';
import Avatar from '../components/avatar';
import AvatarText from '../components/avatar-text';
import ButtonAction from '../components/button-action';
import ButtonIcon from '../components/button-icon';
import Col from '../components/col';
import Content from '../components/content';
import Footer from '../components/footer';
import FooterCopyright from '../components/footer-copyright';
import FooterLinks from '../components/footer-links';
import FooterLogo from '../components/footer-logo';
import FooterLogoCol from '../components/footer-logo-col';
import FooterRegion from '../components/footer-region';
import FooterRegionCol from '../components/footer-region-col';
import FooterRegionIcon from '../components/footer-region-icon';
import FooterRegionLabel from '../components/footer-region-label';
import Grid from '../components/grid';
import Icon from '../components/icon';
import Icons from '../components/icons';
import Nav from '../components/nav';
import NavBrand from '../components/nav-brand';
import NavBrandInfo from '../components/nav-brand-info';
import NavBrandLogo from '../components/nav-brand-logo';
import NavBrandLogoImage from '../components/nav-brand-logo-image';
import NavLinks from '../components/nav-links';
import NavPanel from '../components/nav-panel';
import NavSkip from '../components/nav-skip';
import NavVersionMenu from '../components/nav-version-menu';
import NavVerticalFooter from '../components/nav-vertical-footer';
import NavVerticalHeader from '../components/nav-vertical-header';
import NavVerticalToggleButton from '../components/nav-vertical-toggle-button';
import Progress from '../components/progress';
import Row from '../components/row';
import Tab from '../components/tab';
import TabItemLabel from '../components/tab-item-label';
import TabList from '../components/tab-list';
import Viewport from '../components/viewport';

// State
import { initialState, actions } from './state';

// Pages
import Index from './pages/index';
import Components from './pages/components';
import Docs from './pages/docs';
import Resources from './pages/resources';

const state = { ...initialState, ...actions };

const globalAlertTimed = subscribe()(({
  globalAlertTimed
}) => (
  <Alert
    autoClose
    alertType={globalAlertTimed.alertType}
    id="global-alert-default"
    onBeforeClose={globalAlertTimed.onBeforeClose}
    position="absolute"
    show={globalAlertTimed.show}
    sidenav={globalAlertTimed.sidenav}
  >
    <Grid>
      <Row>
        <Col>
          <AlertIcon>
            {
              globalAlertTimed.icon
                ? globalAlertTimed.icon
                : <Icon name="info" resolution="low" />
            }
          </AlertIcon>
          <AlertLabel>{globalAlertTimed.label}</AlertLabel>
        </Col>
      </Row>
    </Grid>
  </Alert>)
);

const globalAlertDismissable = subscribe()(({
  globalAlertDismissable
}) => (
  <Alert
    alertType={globalAlertDismissable.alertType}
    dismissable
    id="global-alert-dismissable"
    position="absolute"
    show={globalAlertDismissable.show}
    sidenav={globalAlertDismissable.sidenav}
    triggerRef={globalAlertDismissable.triggerRef}
  >
    <Grid>
      <Row>
        <Col>
          <AlertIcon>
            {
              globalAlertDismissable.icon
                ? globalAlertDismissable.icon
                : <Icon name="info" resolution="low" />
            }
          </AlertIcon>
          <AlertLabel>
            <AlertLabelTitle>{globalAlertDismissable.labelTitle}</AlertLabelTitle>
            {globalAlertDismissable.label}
          </AlertLabel>
        </Col>
        <Col sm={1}>
          {globalAlertDismissable.button}
        </Col>
      </Row>
    </Grid>
  </Alert>
));

const nav = subscribe()(({
  nav,
  toggleNav
}) => (
  <Nav orientation="vertical" size={nav.open ? undefined : 'mini'}>
    <NavPanel>
      <NavVerticalHeader>
        <NavSkip href="#content">Skip to main content</NavSkip>
        <NavBrand>
          <NavBrandInfo>
            <ButtonIcon ariaLabel="menu" iconType="dark">
              <Icon name="menu" resolution="low" />
            </ButtonIcon>
            <NavBrandLogo href="/">
              <NavBrandLogoImage alt="Visa" src="http://ux/projects/visa-ui-web/3.0.0-alpha.12/_logos/visa/visa_vbm_wht.svg" />
            </NavBrandLogo>
          </NavBrandInfo>
        </NavBrand>
      </NavVerticalHeader>
      <NavLinks ariaLabel="Site Navigation">
        <TabList orientation="vertical">
          <Tab tag={<NavLink exact to={`${process.env.PUBLIC_URL}/`} activeClassName="vds-state--selected" />} >
            <Icon name="savings-account" resolution="low" /> <TabItemLabel>Info</TabItemLabel>
          </Tab>
          <Tab tag={<NavLink exact to={`${process.env.PUBLIC_URL}/docs`} activeClassName="vds-state--selected" />}>
            <Icon name="note" resolution="low" /> <TabItemLabel>Docs</TabItemLabel>
          </Tab>
          <Tab tag={<NavLink to={`${process.env.PUBLIC_URL}/components`} activeClassName="vds-state--selected" />}>
            <Icon name="view-grid" resolution="low" /> <TabItemLabel>Components</TabItemLabel>
          </Tab>
          <Tab tag={<NavLink exact to={`${process.env.PUBLIC_URL}/resources`} activeClassName="vds-state--selected" />}>
            <Icon name="folder-multiple" resolution="low" /> <TabItemLabel>Resources</TabItemLabel>
          </Tab>
          <Tab tag={<a href="https://visa-charts-lib.k8s-np-cls1-p.trusted.visa.com/" />}>
            <Icon name="analytics" resolution="low" /> <TabItemLabel>Charts Library</TabItemLabel>
          </Tab>
        </TabList>
      </NavLinks>
      <NavVerticalFooter>
        <NavVersionMenu>
          <Avatar><AvatarText>{process.env.REACT_APP_VERSION}</AvatarText></Avatar>
        </NavVersionMenu>
        <NavVerticalToggleButton onClick={toggleNav}>
          <ButtonAction>
            {
              nav.open
                ? <Icon name="arrow-previous" resolution="tiny" />
                : <Icon name="arrow-next" resolution="tiny" />
            }
          </ButtonAction>
        </NavVerticalToggleButton>
      </NavVerticalFooter>
    </NavPanel>
  </Nav>
));

ReactDOM.render(
  <Provider {...state}>
    <Router>
      {globalAlertTimed()}
      {globalAlertDismissable()}
      <Viewport>
        {nav()}
        <Content containsStickyFooter>
          <Route exact path={`${process.env.PUBLIC_URL}/`} component={subscribe()(Index)} />
          <Route exact path={`${process.env.PUBLIC_URL}/components`} component={subscribe()(Components)} />
          <Route exact path={`${process.env.PUBLIC_URL}/docs`} component={subscribe()(Docs)} />
          <Route exact path={`${process.env.PUBLIC_URL}/resources`} component={subscribe()(Resources)} />
          <Suspense
            fallback={
              <Grid siblingOfStickyFooter>
                <Progress
                  style={{
                    left: '0',
                    position: 'absolute',
                    top: '0',
                    width: '100%'
                  }}
                />
              </Grid>
            }
          >
            {
              state.components.map((component, k) => (
                <React.Fragment key={k}>
                  <Route
                    component={subscribe()(React.lazy(() => import(`./pages/components/${component.name}`)))}
                    exact
                    path={`${process.env.PUBLIC_URL}/components/${component.name}`}
                  />
                  <Route
                    component={subscribe()(React.lazy(() => import(`./pages/components/${component.name}`)))}
                    exact
                    path={`${process.env.PUBLIC_URL}/components/${component.name}/code`}
                  />
                </React.Fragment>
              ))
            }
          </Suspense>
          <Footer sticky position="relative" className="styleguide-navbar-example">
            <Grid>
              <Row noGutter>
                <FooterLogoCol xs={4} md={4} lg={6}>
                  <FooterLogo src="http://ux/projects/visa-ui-web/3.0.0-alpha.11/_logos/visa/visa_vbm_blu.svg" alt="Visa logo" />
                </FooterLogoCol>
                <FooterRegionCol xs={4} lg={6} valign="center" lgTextAlign="right" xlTextAlign="right" xxlTextAlign="right">
                  <FooterRegion>
                    <FooterRegionIcon>
                      <Icon name="global" resolution="low" />
                    </FooterRegionIcon>
                    <FooterRegionLabel>United States English</FooterRegionLabel>
                  </FooterRegion>
                </FooterRegionCol>
              </Row>
              <Row noGutter>
                <Col xs={4} md={3} lg={4} xl={3}>
                  <FooterCopyright>
                    {`Copyright  ${new Date().getFullYear()} Visa Inc. All Rights Reserved`}
                  </FooterCopyright>
                </Col>
                <Col xs={4} md={5} lg={8} xl={9}>
                  <FooterLinks>
                    <a href="/">Privacy Policy</a>
                    <a href="/">Terms of Use</a>
                    <a href="/">Adjust Ad Preferences</a>
                  </FooterLinks>
                </Col>
              </Row>
            </Grid>
          </Footer>
        </Content>
        <Icons />
      </Viewport>
    </Router>
  </Provider>,
  document.getElementById('root')
);
// registerServiceWorker();
