import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-links';

const NavLinks = forwardRef(({
  ariaLabel,
  children,
  className,
  role,
  tag: Tag,
  ...remainingProps
}, ref) => (
  <Tag
    aria-label={ariaLabel}
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </Tag>
));

NavLinks.propTypes = {
  /**
   * Aria label
   */
  ariaLabel: string.isRequired,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  role: string,

  /**
  * Tag
  */
  tag: elementType
};

NavLinks.defaultProps = {
  role: 'navigation',
  tag: 'nav'
};

NavLinks.displayName = 'NavLinks';

export default NavLinks;
