import React from 'react';
import { bool, number, object, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-icon';
const ICON_SIZE = {
  high: 48,
  low: 24,
  tiny: 16
};

const Icon = ({ ariaHidden, ariaLabel, className, name, resolution, role, style, viewBox, ...remainingProps }) => (
  <svg
    aria-hidden={ariaHidden}
    aria-label={ariaLabel}
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${resolution}`]: Boolean(resolution)
    }, className)}
    viewBox={`0 0 ${viewBox || ICON_SIZE[resolution]} ${viewBox || ICON_SIZE[resolution]}`}
    focusable="false"
    role={role}
    tabIndex={-1}
    style={style}
    {...remainingProps}
  >
    <use href={`#${name}--${resolution}`} xlinkHref={`#${name}--${resolution}`} />
  </svg>
);

Icon.propTypes = {
  /**
   * Override the default to hide icon from screen readers
   */
  ariaHidden: bool,

  /**
   * @ignore
   */
  ariaLabel: string,

  /**
   * @ignore
   */
  className: string,

  /**
   * Name of the icon e.g. help
   */
  name: string,

  /**
   * Resolution of the icon e.g. help
   */
  resolution: oneOf(['high', 'low', 'tiny']),

  /**
   * A11y role
   */
  role: string,

  /**
   * Style to apply to the icon
   */
  style: object, // eslint-disable-line

  /**
   * ViewBox size
   */
  viewBox: number
};

Icon.defaultProps = {
  ariaHidden: true,
  name: 'help',
  resolution: 'low'
};

Icon.displayName = 'Icon';

export default Icon;
