import theme from '@visa/vds-web/dist/_json/theme';
const components = require('./components');

const getInitialState = () => ({
  componentStatuses: [
    {
      description: 'Development will start in following sprints.',
      isStage: true,
      stage: 'queued'
    },
    {
      description: 'Currently working on it',
      isStage: true,
      stage: 'development'
    },
    {
      description: 'Usability and accessibility testing',
      isStage: true,
      stage: 'testing'
    },
    {
      description: 'Tested and approved.',
      isStage: true,
      stage: 'ready'
    },
    {
      description: 'Component no longer supported.',
      isStage: false,
      stage: 'deprecated'
    }
  ],
  componentTestStatuses: [
    {
      description: 'There are currently no tests for the component.',
      isStage: true,
      stage: 'untested'
    },
    {
      description: 'Component passes aXe testing',
      isStage: true,
      stage: 'aXe Passing'
    },
    {
      description: 'Component passes VGAR/WCAG testing',
      isStage: true,
      stage: 'VGAR Compliant'
    }
  ],
  globalAlertDismissable: {
    alertType: undefined,
    button: undefined,
    icon: undefined,
    label: undefined,
    labelTitle: undefined,
    show: false,
    sidenav: 'open',
    triggerRef: undefined
  },
  globalAlertTimed: {
    alertType: undefined,
    icon: undefined,
    label: undefined,
    onBeforeClose: undefined,
    show: false,
    sidenav: 'open'
  },
  nav: {
    open: true,
    show: true
  },
  theme: {
    vds: JSON.parse(JSON.stringify(theme))
  },
  ...components
});

export const actions = {
  hideGlobalAlertDismissable: () => state => ({
    globalAlertDismissable: {
      ...state.globalAlertDismissable,
      show: false
    }
  }),
  hideGlobalAlertTimed: () => state => ({
    globalAlertTimed: {
      ...state.globalAlertTimed,
      show: false
    }
  }),
  setTheme: vds => state => ({
    theme: {
      vds: {
        ...state.theme.vds,
        ...vds
      }
    }
  }),
  showAlertDismissable: alert => state => ({
    globalAlertDismissable: {
      ...state.globalAlertDismissable,
      ...alert
    }
  }),
  showAlertTimed: alert => state => ({
    globalAlertTimed: {
      ...state.globalAlertTimed,
      ...alert
    }
  }),
  toggleNav: () => state => ({
    nav: {
      ...state.nav,
      open: !state.nav.open
    }
  })
};

export const initialState = getInitialState();
