import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-vertical-header';

const NavVerticalHeader = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

NavVerticalHeader.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

NavVerticalHeader.defaultProps = {
  tag: "section"
};

NavVerticalHeader.displayName = 'NavVerticalHeader';

export default NavVerticalHeader;
