import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-brand-info';

const NavBrandInfo = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

NavBrandInfo.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

NavBrandInfo.displayName = 'NavBrandInfo';

export default NavBrandInfo;
