import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-avatar--text';

const AvatarText = ({ children, className, textShade, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${textShade}`]: Boolean(textShade)
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

AvatarText.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * textShade
   */
  textShade: oneOf(['dark', 'light'])
};

AvatarText.displayName = 'AvatarText';

export default AvatarText;
