import React, { Children, cloneElement, forwardRef, useEffect, useRef, useState } from 'react';
import { node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import TabItem from '../tab-item';

const CSS_PREFIX = 'vds-tab';

const TabList = forwardRef(({
  children,
  className,
  orientation,
  role,
  selectedTab,
  ...remainingProps
}, ref) => {
  const [selectedTabIndex, setSelectedTabIndex] = useState(selectedTab);
  const [focusedIndex, setFocusedIndex] = useState(selectedTabIndex);
  const childRefs = [];

  useEffect(() => {
    setSelectedTabIndex(selectedTab);
  }, [selectedTab]);

  const handleClick = (e, k) => {
    setSelectedTabIndex(k);
    setFocusedIndex(k);
  };

  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const focusableChildren = childRefs.filter(child => !child.current.classList.contains('vds-state--disabled'));
    const count = focusableChildren.length;
    const fIndex = focusedIndex ? focusedIndex : 0;

    if (code === 35) {
      e.preventDefault();
      setFocusedIndex(count - 1);
      return focusableChildren[count - 1].current.focus();
    }

    if (code === 36) {
      e.preventDefault();
      setFocusedIndex(0);
      return focusableChildren[0].current.focus();
    }

    if (code === 37 || code === 38) {
      e.preventDefault();
      setFocusedIndex(fIndex - 1 < 0 ? count - 1 : fIndex - 1);
      return focusableChildren[fIndex - 1 < 0 ? count - 1 : fIndex - 1].current.focus();
    }

    if (code === 39 || code === 40) {
      e.preventDefault();
      setFocusedIndex((fIndex + 1) % count);
      return focusableChildren[(fIndex + 1) % count].current.focus();
    }

    return setFocusedIndex(selectedTabIndex);
  };

  return (
    <ul
      className={classnames(`${CSS_PREFIX}-list`, {
        [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation)
      }, className)}
      ref={ref}
      role={role}
      {...remainingProps}
    >
      {
        Children.map(children, (child, k) => {
          const childRef = useRef();
          childRefs.push(childRef);

          return (
            <TabItem role="none">
              {
                cloneElement(child, {
                  key: k,
                  onClick: e => {
                    handleClick(e, k);
                    if (typeof child.props.onClick === 'function') {
                      child.props.onClick(e, k);
                    }
                  },
                  onKeyDown: e => {
                    handleKeyDown(e, k);
                    if (typeof child.props.onKeyDown === 'function') {
                      child.props.onKeyDown(e, k);
                    }
                  },
                  ref: childRef,
                  selected: selectedTabIndex === k,
                  tabIndex: typeof selectedTabIndex === 'undefined' ? k === 0 ? undefined : -1 : selectedTabIndex === k ? undefined : -1
                })
              }
            </TabItem>
          );
        })
      }
    </ul>
  );
});

TabList.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Orientation
   */
  orientation: oneOf(['vertical']),

  /**
   * @ignore
   */
  role: string,

  /**
   * Selected Tab Index
   */
  selectedTab: oneOfType([number, string])
};

TabList.defaultProps = {
  role: 'tablist'
};

TabList.displayName = 'TabList';

export default TabList;
