import React from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-grid';

// send to VDS Web
const stickyStyles = {
  flexGrow: '1'
};

const Grid = ({ children, className, type, siblingOfStickyFooter, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${type}`]: Boolean(type)
    }, className )}
    style={
      siblingOfStickyFooter
        ? remainingProps.styles
          ? { ...stickyStyles, ...remainingProps.styles }
          : stickyStyles
        : remainingProps.styles
          ? remainingProps.styles
          : undefined
    }
    {...remainingProps}
  >
    {children}
  </div>
);

Grid.propTypes = {
  /**
   * Content
   */
  children: node,

  /**
   * Additional class
   */
  className: string,

  /**
   * Is sibling of a sticky footer
   */
  siblingOfStickyFooter: bool,

  /**
   * Grid type
   */
  type: oneOf(['fluid'])
};

Grid.displayName = 'Grid';

export default Grid;
