import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';
import Col from '../col';

const CSS_PREFIX = 'vds-footer-logo-col';

const FooterLogoCol = ({ children, className, ...remainingProps }) => (
  <Col
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </Col>
);

FooterLogoCol.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

FooterLogoCol.displayName = 'FooterLogoCol';

export default FooterLogoCol;
