import React, { forwardRef } from 'react';
import { elementType, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-viewport';

const Viewport = forwardRef(({
  children,
  className,
  id,
  role,
  tag: Tag,
  ...remainingProps
}, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    id={id}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </Tag>
));

Viewport.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Id
   */
  id: oneOfType([number, string]),

  /**
   * @ignore
   */
  role: string,

  /**
  * Tag
  */
  tag: elementType
};

Viewport.defaultProps = {
  id: 'content',
  role: 'main',
  tag: 'main'
};

Viewport.displayName = 'Viewport';

export default Viewport;
