import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-alert-label';

const AlertLabel = ({ ariaLive, ariaAtomic, children, className, ...remainingProps }) => (
  <span
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

AlertLabel.propTypes = {
  /**
   * Aria Atomic
   */
  ariaAtomic: oneOf(['true', 'false']),

  /**
   * Aria Live
   */
  ariaLive: oneOf(['assertive', 'polite', 'off']),

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

AlertLabel.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'polite'
};

AlertLabel.displayName = 'AlertLabel';

export default AlertLabel;
