import React from 'react';
import { bool, number, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-stage';
const CSS_STATE_COMPLETE = 'vds-state--complete';

const Stage = ({
  className,
  currentStage,
  currentStageClassname,
  currentStageError,
  currentStageLabel,
  showLabel,
  stages,
  ...remainingProps
}) => (
  <div className={classnames(`${CSS_PREFIX}-container`, className)} {...remainingProps}>
    <div className={CSS_PREFIX}>
      {[...Array(stages)].map((x, i) => (
        <div
          key={i}
          className={classnames(`${CSS_PREFIX}-step`, {
            [`${CSS_STATE_COMPLETE}-${currentStage}`]: i === currentStage,
            [`${currentStageClassname}`]: currentStageClassname ? i === currentStage : undefined,
            [`vds-state--complete-x`]: i === currentStage && Boolean(currentStageError)
          })}
        />
      ))}
    </div>
    {showLabel && stages > 0 ? <span className={`${CSS_PREFIX}-label`}>{currentStageLabel}</span> : null}
  </div>
);

Stage.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Current stage
   */
  currentStage: number.isRequired,

  /**
   * Current stage classname
   */
  currentStageClassname: string,

  /**
   * Current stage error
   */
  currentStageError: bool,

  /**
   * Label
   */
  currentStageLabel: string.isRequired,

  /**
   * Show label
   */
  showLabel: bool,

  /**
   * Stages
   */
  stages: number
};

Stage.defaultProps = {
  currentStage: 0,
  currentStageLabel: 'Stage 0',
  showLabel: true,
  stages: 2
};

Stage.displayName = 'Stage';

export default Stage;
