import React, { cloneElement, forwardRef, Fragment } from 'react';
import { element, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-avatar';

const Avatar = forwardRef(({
  children,
  className,
  tag,
  initials,
  label,
  ...remainingProps
}, ref) => {
  const initialsMarkup = (initials, label) => (
    <Fragment>
      <div className={`${CSS_PREFIX}--label`}>{label || initials}</div>
      <div className={`${CSS_PREFIX}--initials`} aria-hidden="true" role="presentation">
        {initials}
      </div>
    </Fragment>
  );

  return (
    cloneElement(
      tag,
      {
        children: initials && label ? initialsMarkup(initials, label) : children,
        className: classnames(CSS_PREFIX, className),
        ref,
        ...remainingProps
      }
    )
  );
});

Avatar.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Initials
   */
  initials: string,

  /**
   * Label
   */
  label: string,

  /**
  * Tag
  */
  tag: element
};

Avatar.defaultProps = {
  tag: <div />
};

Avatar.displayName = 'Avatar';

export default Avatar;
