import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';
import Typography from '../typography';

const CSS_PREFIX = 'vds-footer-region-label';

const FooterRegionLabel = forwardRef(({ children, className, tag, variant, ...remainingProps }, ref) => (
  <Typography
    className={classnames(CSS_PREFIX, className)}
    variant={variant}
    ref={ref}
    tag={tag}
    {...remainingProps}
  >
    {children}
  </Typography>
));

FooterRegionLabel.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType,

  /**
   * Variant
   */
  variant: string
};

FooterRegionLabel.defaultProps = {
  variant: 'caption'
};

FooterRegionLabel.displayName = 'FooterRegionLabel';

export default FooterRegionLabel;
