import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-version-menu';

const NavVersionMenu = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

NavVersionMenu.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

NavVersionMenu.defaultProps = {
};

NavVersionMenu.displayName = 'NavVersionMenu';

export default NavVersionMenu;
