import React from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-footer-region-icon';

const FooterRegionIcon = ({
  children,
  className,
  tag: Tag,
  ...remainingProps
}) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </Tag>
);

FooterRegionIcon.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

FooterRegionIcon.defaultProps = {
  tag: 'span'
};

FooterRegionIcon.displayName = 'FooterRegionIcon';

export default FooterRegionIcon;
