import React from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'row';

const Row = ({ children, className, noGutter, textAlign, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`vds-text--${textAlign}`]: Boolean(textAlign),
      [`col--no-gutter`]: noGutter
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>);

Row.propTypes = {
  /**
   * Content
   */
  children: node,
  /**
   * Additional class
   */
  className: string,

  /**
   * noGutter
   */
  noGutter: bool,

  /**
   * Text alignment
   */
  textAlign: oneOf(['center', 'left', 'right'])
};

Row.displayName = 'Row';

export default Row;
