import React, { cloneElement, forwardRef } from 'react';
import { bool, element, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-brand-logo';
const CSS_STATE = 'vds-state';

const NavBrandLogo = forwardRef(({
  className,
  disabled,
  tag,
  ...remainingProps
}, ref) => (
  cloneElement(tag, {
    className: classnames(CSS_PREFIX, {
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className),
    ref,
    ...remainingProps
  })
));

NavBrandLogo.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Disabled
  */
  disabled: bool,

  /**
  * Tag
  */
  tag: element
};

NavBrandLogo.defaultProps = {
  tag: <a /> // eslint-disable-line
};

NavBrandLogo.displayName = 'NavBrandLogo';

export default NavBrandLogo;

