import React, { cloneElement, forwardRef } from 'react';
import { bool, element, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav-brand-logo-image';
const CSS_STATE = 'vds-state';

const NavBrandLogoImage = forwardRef(({
  className,
  disabled,
  tag,
  ...remainingProps
}, ref) => (
  cloneElement(tag, {
    className: classnames(CSS_PREFIX, {
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className),
    ref,
    ...remainingProps
  })
));

NavBrandLogoImage.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Disabled
  */
  disabled: bool,

  /**
  * Tag
  */
  tag: element
};

NavBrandLogoImage.defaultProps = {
  tag: <img /> // eslint-disable-line
};

NavBrandLogoImage.displayName = 'NavBrandLogoImage';

export default NavBrandLogoImage;

