import React from 'react';
import { arrayOf, shape } from 'prop-types';
import classnames from 'classnames';
import { NavLink } from 'react-router-dom';
import Grid from '../../../components/grid';
import Row from '../../../components/row';
import Col from '../../../components/col';
import Typography from '../../../components/typography';
import Icon from '../../../components/icon';
import Stage from '../../../components/stage';

const changeCase = require('change-case');

const Components = ({ components, componentStatuses }) => (
  <Grid siblingOfStickyFooter>
    <Row>
      <Col>
        <Typography tag="h1">Modular components</Typography>
      </Col>
    </Row>
    <Row>
      <Col xs={4} sm={8} md={6}>
        <table
          className={classnames([
            'vds-data-table',
            'vds-state--single-select',
            'vds-state--compact',
            'vds-state--divider-lines',
            'components'
          ])}
          data-header="header"
        >
          <caption className="vds-sr">List of components</caption>
          <thead className="vds-thead">
            <tr className="vds-tr">
              <th className="vds-th" aria-sort="none" role="columnheader" scope="col">
                Component
              </th>
              <th className="vds-th" aria-sort="none" role="columnheader" scope="col">
                Status <span className="vds-datagrid-header--sort">
                  <Icon name="info" resolution="low" />
                </span>
              </th>
              <th className="vds-th" aria-sort="none" role="columnheader" scope="col">
                VGAR/WCAG <span className="vds-datagrid-header--sort">
                  <Icon name="info" resolution="low" />
                </span>
              </th>
              <th className="vds-th" aria-sort="none" colSpan="2" role="columnheader" scope="col">
                Resources
              </th>
            </tr>
          </thead>
          <tbody className={classnames(['vds-tbody', 'modular-components'])}>
            {components.map((component, i) => (
              <tr className="vds-tr" key={i} tabIndex="0">
                <td className="vds-td">
                  <NavLink
                    to={`${process.env.PUBLIC_URL}/components/${component.name}`}
                  >
                    {changeCase.titleCase(component.name)}
                  </NavLink>
                </td>
                <td className="vds-td">
                  <Stage
                    stages={componentStatuses.filter(s => s.isStage).length}
                    currentStageLabel={changeCase.titleCase(component['vds-react-status'])}
                    currentStage={componentStatuses.findIndex(s => s.stage === component['vds-react-status'])}
                    currentStageClassname={classnames({
                      [`vds-state--complete-2`]: component['vds-react-status'] === 'development',
                      [`vds-state--complete-4`]: component['vds-react-status'] === 'testing',
                      [`vds-state--complete-8`]: component['vds-react-status'] === 'ready'
                    })}
                    currentStageError={Boolean(component['vds-react-status'] === 'deprecated')}
                  />
                </td>
                <td className="vds-td">
                  <Stage
                    stages={3}
                    currentStageLabel={`${(component['vds-react-vgar'] && 'VGAR Compliant') ||
                      (component['vds-react-axe'] && 'aXe Passing') ||
                      (component['vds-react-axe'] === false && 'aXe Failing')}`}
                    currentStage={(component['vds-react-vgar'] && 2) || 1}
                    currentStageError={component['vds-react-axe'] === false}
                    currentStageClassname={classnames({
                      [`vds-state--accessible--vgar`]: Boolean(component['vds-react-vgar'])
                    })}
                  />
                </td>
                <td className="vds-td">
                  <a
                    href={`${process.env.PUBLIC_URL}/component/index.html#${changeCase.lowerCase(
                      changeCase.camelCase(component.name)
                    )}`}
                  >
                    <Icon name="code-fork-code-alt" resolution="low" /> Docs
                  </a>
                </td>
                <td className="vds-td">
                  <a href={component['vds-react-guidelines']}>
                    <Icon name="glossary" resolution="low" /> Design
                  </a>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </Col>
      <Col xs={4} sm={8} md={2} className="sidebar">
        Latest: <span className="styleguide-version">{process.env.REACT_APP_VERSION}</span>
        <Typography tag="h2">Status Key</Typography>
        <ul>
          {componentStatuses.map((s, i) => (
            <li key={i}>
              <strong>{changeCase.titleCase(s.stage)}</strong>: {s.description}
            </li>
          ))}
        </ul>
      </Col>
    </Row>
  </Grid>
);

Components.propTypes = {
  /**
   * Component statuses
   */
  componentStatuses: arrayOf(shape),

  /**
   * List of components
   */
  components: arrayOf(shape)
};

Components.defaultProps = {};

export default Components;
