import React, { forwardRef, useEffect, useRef } from 'react';
import { bool, func, node, number, oneOf, shape, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-alert';
const CSS_STATE = 'vds-state';

const Alert = forwardRef(({
  alertType,
  autoClose,
  autoCloseTimeout,
  children,
  className,
  dismissable,
  onBeforeClose,
  position,
  role,
  show,
  sidenav,
  triggerRef,
  ...remainingProps
}, ref) => {
  ref = ref || useRef();

  useEffect(() => {
    if (autoClose && show && Number(autoCloseTimeout)) {
      setTimeout(() => onBeforeClose(), autoCloseTimeout);
    }
  }, [autoClose, autoCloseTimeout, onBeforeClose, show]);

  useEffect(() => {
    if (dismissable && show && ref && ref.current) {
      ref.current.querySelector(
        'button:not([disabled]):not(.vds-state--disabled)'
      ).focus();
    }

    if (dismissable && !show && triggerRef && triggerRef.current) {
      triggerRef.current.focus();
    }
  }, [dismissable, ref, show, triggerRef]);

  return (
    <div
      className={classnames(CSS_PREFIX, {
        [`${CSS_PREFIX}--${alertType}`]: Boolean(alertType),
        [`${CSS_PREFIX}--${position}`]: Boolean(position),
        [`${CSS_STATE}--show`]: Boolean(show),
        [`${CSS_PREFIX}--sidenav--${sidenav}`]: Boolean(sidenav)
      }, className)}
      ref={ref}
      role={role}
      {...remainingProps}
    >
      {children}
    </div>
  );
});

Alert.propTypes = {
  /**
   * Alert Type
   */
  alertType: oneOf(['error', 'information', 'success', 'warning']),

  /**
   * Auto-close
   */
  autoClose: bool,

  /**
   * Auto-close Timeout
   */
  autoCloseTimeout: number,

  /**
   * @ignore
   */
  children: node.isRequired,

  /**
   * @ignore
   */
  className: string,

  /**
   * Dismissable Alert
   */
  dismissable: bool,

  /**
   * On before close
   */
  onBeforeClose: func,

  /**
   * Position
   */
  position: oneOf(['absolute', 'relative']),

  /**
   * @ignore
   */
  role: string,

  /**
   * Show
   */
  show: bool,

  /**
   *  Sidenav state
   */
  sidenav: oneOf(['close', 'open']),

  /**
   *  Ref of button that opened the Alert for focus back when Alert closes
   */
  triggerRef: shape({})
};

Alert.defaultProps = {
  autoCloseTimeout: 3000,
  role: 'alert'
};

Alert.displayName = 'Alert';

export default Alert;
