import React, { cloneElement, forwardRef } from 'react';
import { bool, element, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-tab';
const CSS_STATE = 'vds-state';

const Tab = forwardRef(({
  className,
  disabled,
  selected,
  size,
  tag,
  ...remainingProps
}, ref) => (
  cloneElement(tag, {
    'aria-current': tag.type === 'a' && Boolean(selected) ? 'page' : undefined,
    'aria-disabled': tag.type === 'a' && Boolean(disabled) ? true : undefined,
    'aria-selected': tag.type === 'button' ? selected ? 'true' : 'false' : undefined,
    className: classnames(CSS_PREFIX, {
      [`${CSS_STATE}--disabled`]: Boolean(disabled),
      [`${CSS_STATE}--selected`]: Boolean(selected),
      [`${CSS_PREFIX}--${size}`]: Boolean(size)
    }, className),
    disabled,
    ref: (tag.type === 'a' || tag.type === 'button') ? ref : undefined, // TODO: change to just ref after publish of https://github.com/ReactTraining/react-router/pull/6914
    role: tag.props.role || (tag.type === 'button' ? 'tab' : undefined),
    ...(tag.type === 'a' || tag.type === 'button') ? undefined : { innerRef: ref }, // TODO: Delete after publish of https://github.com/ReactTraining/react-router/pull/6914
    ...remainingProps
  })
));

Tab.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
  * Disabled
  */
  disabled: bool,

  /**
  * Selected
  */
  selected: bool,

  /**
   * Size
   */
  size: oneOf(['tall']),

  /**
  * Tag
  */
  tag: element
};

Tab.defaultProps = {
  tag: <button /> // eslint-disable-line
};

Tab.displayName = 'Tab';

export default Tab;
