import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-tab-item-label';

const TabItemLabel = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

TabItemLabel.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

TabItemLabel.defaultProps = {
};

TabItemLabel.displayName = 'TabItemLabel';

export default TabItemLabel;
