import React, { forwardRef } from 'react';
import { elementType, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-nav';

const Nav = forwardRef(({
  children,
  className,
  orientation,
  position,
  size,
  tag: Tag,
  type,
  ...remainingProps
}, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation),
      [`${CSS_PREFIX}--${position}`]: Boolean(position),
      [`${CSS_PREFIX}--${type}`]: Boolean(type),
      [`${CSS_PREFIX}-vertical-${size}`]: Boolean(size)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

Nav.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Orientation
   */
  orientation: oneOf(['vertical']),

  /**
   * Position
   */
  position: oneOf(['absolute', 'fixed', 'relative']),

  /**
   * Size
   */
  size: oneOf(['mini']),

  /**
  * Tag
  */
  tag: elementType,

  /**
   * Type
   */
  type: oneOf(['consumer', 'marketing'])
};

Nav.defaultProps = {
  tag: 'header'
};

Nav.displayName = 'Nav';

export default Nav;
