import React, { forwardRef } from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-btn-split';
const CSS_STATE = 'vds-state';

const ButtonSplit = forwardRef(({
  active,
  children,
  className,
  colorScheme,
  disabled,
  flippable,
  type,
  ...remainingProps
}, ref) => (
  <button
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${colorScheme}`]: Boolean(colorScheme),
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--flippable`]: Boolean(flippable),
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className)}
    ref={ref}
    type={type}
    {...remainingProps}
  >
    {children}
  </button>
));

ButtonSplit.propTypes = {
  /**
   * Active
   */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Color scheme of button
   */
  colorScheme: oneOf([
    'primary',
    'secondary'
  ]),

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Flippable
   */
  flippable: bool,

  /**
   * @ignore
   */
  type: string
};

ButtonSplit.defaultProps = {
  type: 'button'
};

ButtonSplit.displayName = 'ButtonSplit';

export default ButtonSplit;
