import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';
import ButtonSplit from '../button-split';

const CSS_PREFIX = 'vds-nav-vertical-toggle-button';

const NavVerticalToggleButton = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <ButtonSplit
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </ButtonSplit>
));

NavVerticalToggleButton.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

NavVerticalToggleButton.defaultProps = {
};

NavVerticalToggleButton.displayName = 'NavVerticalToggleButton';

export default NavVerticalToggleButton;
